// ContentBox.js
import React from 'react';
import PropTypes from 'prop-types';

import Home from '../Home/Home';
import JobBox from '../Jobs/JobBox';
import CommentBox from '../Comments/CommentBox';
import TvShowBox from '../TvShows/TvShowBox';
import Login from '../Login/Login';
import TripleClick from '../TripleClick/TripleClick';
import GitGraphWidget from '../GitGraph/GitGraphWidget';
import Python from '../Python/Python';

const ContentBox = (props) => {
  const { selectedMenu } = props;
  if (selectedMenu) {
    switch (props.selectedMenu) {
      case 'Jobs':
        return (<JobBox />);
      case 'Comments':
        return (<CommentBox />);
      case 'Shows':
        return (<TvShowBox />);
      case 'Login':
        return (<Login />);
      case 'Triple Click':
        return (<TripleClick />);
      case 'Git Graph':
        return (<GitGraphWidget />);
      case 'Python':
        return (<Python />);
      default:
        break;
    }
  }

  return (<Home />);
};

ContentBox.propTypes = {
  selectedMenu: PropTypes.string.isRequired,
};

export default ContentBox;
