// TripleClick.js
import { fromEvent } from 'rxjs';
import {
  bufferWhen, filter, map, delay,
} from 'rxjs/operators';
import React, { Component } from 'react';
import './TripleClick.css';

class TripleClick extends Component {
  componentDidMount() {
    const output = document.querySelector('.Output');
    // Get the stream of clicks.
    const click$ = fromEvent(document.getElementById('clickMe'), 'click');
    // Buffer clicks every 400 ms.
    const bufferClick$ = click$.pipe(bufferWhen(() => click$.pipe(delay(400))), map(arr => arr.length));

    bufferClick$.pipe(filter(len => len === 1)).subscribe(() => {
      output.textContent = 'Single Click';
    });

    bufferClick$.pipe(filter(len => len === 2)).subscribe(() => {
      output.textContent = 'Double Click';
    });

    // If a 3 clicks occur within 400 ms we detected a triple click!
    bufferClick$.pipe(filter(len => len > 2)).subscribe(() => {
      output.innerHTML = 'Triple Click!<br /><img alt="Mind Blown" src="mind_blown.jpg" />';
    });
  }

  render() {
    return (
      <div className="TripleClick">
        <button className="buttonR" id="clickMe" type="button">Click ME</button>
        <div className="Output" />
      </div>
    );
  }
}

export default TripleClick;
