// Login.js
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import FacebookLogin from 'react-facebook-login';

// Note: Get your own app id from FB and set it in this file.
import { AppId } from './FBConstants';

import './Login.css';

const RenderFBProfile = (props) => {
  const { picture, name } = props;
  if (picture && name) {
    return (
      <div className="FBProfile">
        <img src={picture} alt="Profile Foto" />
        &nbsp;
        {name}
      </div>);
  }

  return (<div className="EmptyFBProfile" />);
};

RenderFBProfile.propTypes = {
  picture: PropTypes.string,
  name: PropTypes.string,
};

RenderFBProfile.defaultProps = {
  picture: '',
  name: '',
};

class Login extends Component {
  constructor() {
    super();
    this.state = {
      picture: '',
    };
  }

  responseFacebook = (response) => {
    console.log(response);
    if (response.status === 'unknown') {
      console.log('closed window');
    } else if (response.picture.data.url && response.name) {
      this.setState({ picture: response.picture.data.url, name: response.name });
      return;
    }

    // Reset state if response was invalid.
    this.setState({ picture: '', name: '' });
  };

  render() {
    const { picture, name } = this.state;
    if (picture && name) {
      return (<RenderFBProfile picture={picture} name={name} />);
    }
    return (
      <div className="Login">
        <FacebookLogin
          appId={AppId}
          autoLoad
          fields="name,email,picture"
          callback={this.responseFacebook}
        />
      </div>);
  }
}

export default Login;
